import { Tools } from "@tools/tools"
import "@views/scss/front/cart-summary.scss"
import { Summary } from "@views/ts/front/cart-summary/summary"

declare let __webpack_public_path__: string

if (typeof __webpack_public_path__ !== "undefined") {
  __webpack_public_path__ = (window as any).dp_public_path
}

const cartSummary = new Summary(new Tools())

$(document).on("ready", () => {
  cartSummary.init()
})
